html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.btn {
  font-weight: 500;
  border-radius: 40px;
  outline: none;
  transition: all .3s ease-in-out;
  cursor: pointer;

  &-main {
    padding: 25px 54px;
    font-size: 20px;
    line-height: 20px;

    @include mobile {
      padding: 17px 30px;
      font-size: 16px;
      line-height: 16px;
    }
  }

  &-secondary {
    font-size: 16px;
    line-height: 16px;
  }

  &--stroke {
    border: 2px solid var(--black);
    background-color: transparent;
  }

  &--fill {
    background-color: var(--primary);
    color: var(--white);
    border: 2px solid var(--primary);
  }
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  @include big-desktop {
    padding: 0 100px;
  }

  @include tablet {
    padding: 0 114px;
  }

  @media (max-width: 651px) {
    padding: 0 32px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.link {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--black);
  transition: all .3s ease-in-out;
  outline: none;

  &:hover:not(:focus-visible) {
    color: var(--primary);
  }

  &:active:not(:focus-visible) {
    color: var(--primary-shade);
  }

  &.focus-visible {
    &.focus-visible {
      color: var(--white);
      animation: fadeEffect .3s;
      &::before {
        content: '';
        position: absolute;
        top: -4px;
        left: -10px;
        right: -10px;
        bottom: -4px;
        background-color: var(--primary-light);
        transition: all .3s ease-in-out;
        z-index: -1;
      }
    }
  }
}

.title {
  margin: 0;
  font-weight: 400;
  font-size: 48px;
  line-height: 100%;
  color: var(--black);
  @include mobile {
    font-size: 24px;
  }
}

@keyframes fadeEffect {
  from {opacity: 0;}
  to {opacity: 1;}
}
